.timerDisplay{
    position: relative;
    width: 40%;
    background: #242582;
    left: 30%;
    padding: 40px 0;
    font-family: 'Roboto mono',monospace;
    color: white;
    font-size: 40px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 5px;
    box-shadow: 0 0 20px rgba(0,139,253,0.25);
}

.buttons{
    width: 50%;
    margin: 60px auto 0 auto;
    display: flex;
    justify-content: space-around;
}
.buttons button{
    width: 120px;
    height: 45px;
    background-color: orange;
    color: #ffffff;
    border: none;
    font-family: 'Poppins',sans-serif;
    font-size: 18px;
    border-radius: 5px;
    cursor: pointer;
    outline: none;
}

.buttons button:nth-last-child(2){
    background-color: green;
  }
  .buttons button:nth-last-child(1){
    background-color: red;
  }